import React, { Component } from 'react';
import {
  BrowserRouter as Router,
  Route
} from 'react-router-dom';

import ReallySmoothScroll from 'really-smooth-scroll';

import './App.css';
//component
import Header from './Components/Header';
import Home from './Components/Home';
import Products from './Components/Products';
import About from './Components/About';
import Contact from './Components/Contact'
import Special from './Components/Special';
import Footer from './Components/Footer';

import Arrow from './Assets/arrow-up.png'

//scroll
//ReallySmoothScroll.shim();


class App extends Component {
  constructor(props){
    super(props);
    this.handleClick = this.handleClick.bind(this);

  }

handleClick(){
  window.scroll({
    top: 0,
    behavior: "smooth"
  });

}

  render() {

    return (
      <Router basename={process.env.PUBLIC_URL}>
      <div>
        <Header />


          {["/pocetna", "/"].map((path, index) =>
         <Route exact path={path} component={Home} key={index} />
          )}

        <Route path='/meni' component={Products} />
        <Route path='/kontakt' component={Contact} />
        <Route path='/onama' component={About} />
        <Route path='/specijaliteti' component={Special} />

        <img src={Arrow} alt='arrow up' onClick={this.handleClick} className='scroll-up' />

        <Footer />
      </div>
      </Router>
    );
  }

}

export default App;



